	// 引入定位
import {addressdata} from '../../common/list.js'
// 引入SDK核心类
var QQMapWX = require('../../common/qqmap-wx-jssdk.js');
var qqmapsdk = new QQMapWX({
		key: 'M5IBZ-FPCHS-NM6OI-6CY27-IN2J7-H7FJG'
		});
export default {
	name: 'citying',
	data() {
		return {
			citynone:true,
			address:'',
			citydata:[], //搜索的城市
			keywoeds:'',
			pageroute:'',  //从哪个页面进来的路由
			city: [
				{
				"name":'昆明市'
				},
				{
				"name":'大理白族自治州'
				},
				{
				"name":'北京市'
				},
				{
				"name":'上海市'
				},
				{
				"name":'广州市'
				},
				{
				"name":'深圳市'
				},
				{
				"name":'杭州市'
				}
				],

		};
	},
	methods:{
		// 搜索触发
		searchCity(e){
			// console.log(e)
			this.citynone = false
		},
		// 点击取消按钮，关闭搜索
		canCel(){
			this.citynone = true
			this.keywoeds = ''
			this.citydata = ''
		},
		
		// 点击定位取到城市名称
		clickCity(){
			// console.log(this.address)
			let cityion = this.address
			this.rouTes(cityion)
		},
		
		// 取到热门城市
		hotCity(city){
			// console.log(city)
			this.rouTes(city)
		},
		
		// 搜索城市
		seekCity(city){
			console.log(city)
			this.rouTes(city)
		},
		
		// 跳转到攻略页面
		rouTes(cityion){
			// console.log(cityion)
			// 用vuex传值
			if(this.pageroute == 'pages/travels/travels'){
				console.log(cityion)
				// 传给发表页面的
				this.$store.commit('travemuta',cityion)
			}else{
				// 传给tabr攻略页面的
				console.log(cityion)
				this.$store.commit('citymuta',cityion)
			}
			uni.navigateBack({
			    delta: 1
			});
		},
		
		// 实时搜索城市
		searchInput(e){
			// console.log(e.detail.value)
			qqmapsdk.getSuggestion({
				keyword:e.detail.value,
				success:(res)=>{
					console.log(res)
					let city = res.data
					// 取出城市名
					let thecity = res.data[0].city
					let citydata = city.map((item)=>{
						return item.title
					})
					// 数组合并
					let allcity = [thecity,...citydata]
					// console.log(allcity)
					this.citydata  = allcity
				},
				fail:(err)=>{
					console.log(err)
				}
			})
		},
		
		// 定位当前城市
			addRess(){
				addressdata()
				.then((res)=>{
					console.log(res)
					this.address = res.result.ad_info.city
				})
				.catch((err)=>{
					console.log('用户拒绝定位')
					this.address = '昆明市'
				})
			}
	},
	mounted() {
		this.addRess()
	},
	// 判断路由
	onLoad() {
		let pages = getCurrentPages()
		let prevpage = pages[pages.length - 2]; 
		console.log(prevpage.route)
		this.pageroute = prevpage.route
	}
};
